// @ is an alias to /src

export default {
  name: 'InvoiceOwed',
  components: {
    VueApexCharts: () => import('vue-apexcharts')
  },
  props: {
    title: {
      type: String,
      default: 'New Chart'
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '220px'
    },
    newItemLabel: {
      type: String,
      default: 'New Item'
    },
    showNewItemBtn: {
      type: Boolean,
      default: false
    },
    menu:{
      type:Array,
      default: () => {
        return []
      }
    },
    series: Array,
    stacked: {
      type:Boolean,
      default:true
    },
    plotOptions: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data: () => ({    
    series1: [{
      name: 'series-1',
      data: [30, 40, 45, 50, 49, 60, 70, 91, 74, 80, 105, 125]
    }],
    defaultPlot:{
      bar: {
        startingShape: 'flat',
        endingShape: 'flat',
      }
    },
  }),
  computed:{
    options: {
      get() {
        var merge = require('lodash.merge')
        var _plotOptions = merge({}, this.defaultPlot, this.plotOptions)
        var x = {
          chart: {
            id: 'example',
            stacked: this.stacked,
            sparkline: {
              eenabled: true
            },
            toolbar: {
              tools: {
                download: false
              }
            }
          },
          plotOptions: _plotOptions,
          xaxis: {
            categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
          }
      }
      return x
    },
    parentStyle(){
      return `height:${this.height};padding:0px`
    }
  }
  }
}
